import { Marker, InfoWindow } from "@react-google-maps/api";
import { useState } from "react";

//  + (InfoWindow)
const BreweryMarker = ({ brewery }) => {
  const [open, setOpen] = useState(false); // InfoWindow  . false   
  const { name, location, image, homepage } = brewery;

  const customIcon = {
    url: "/images/beer_marker2.png",
    scaledSize: { width: 50, height: 50 },
  };

  return (
    <>
      <Marker
        position={{ lat: location.y, lng: location.x }}
        icon={customIcon}
        onClick={() => setOpen(true)} //  InfoWindow .
      />
      {open && (
        <InfoWindow
          position={{ lat: location.y, lng: location.x }}
          onCloseClick={() => setOpen(false)} //    InfoWindow 
        >
          <div>
            <h4>{name}</h4>
            <img src={image} alt={name} style={{ width: "120px" }} />
            <p>
              <a href={homepage} target="_blank" rel="noreferrer">
                
              </a>
            </p>
          </div>
        </InfoWindow>
      )}
    </>
  );
};

export default BreweryMarker;

/*
Marker :   ()  
InfoWindow :    .  , ,   
*/
