import { GoogleMap } from "@react-google-maps/api";
import BreweryMarker from "./BreweryMarker";

const containerStyle = { width: "100%", height: "80vh" };
const center = { lat: 35.868469, lng: 127.445945 };

// breweries      
const MapContainer = ({ breweries }) => (
  // LoadScript  Google Maps JS API 
  <GoogleMap mapContainerStyle={containerStyle} center={center} zoom={7.4}>
    {breweries.map((b) => (
      <BreweryMarker key={b.id} brewery={b} />
    ))}
  </GoogleMap>
);

export default MapContainer;
